<!-- import aDailyWord from '@/view/whale-news/a-daily-word.vue'; 每日一言-->
<template>
	 <div class="aDailyWord">
	 	    <div class="aDailyWord_wrap">
	 	    	   <DailyBar :dataList="dataList"/>
	 	    </div>
	 	    <!--日历swiper start-->
	 	      <div class="aDailyWord_calendar">
	 	      	    <div class="swiperBox_top">
				    	   	  <span class="swiperBox_top_year">2018</span>
				    	   	  <span class="swiperBox_top_month">September</span>
						  </div>
		 	      	  <swiper :options="swiperOption" ref="mySwiper">
						    <swiper-slide v-for="(item,index) in monthList"  :key="index" class="swiperBox">
						    	   <div class="selectMonth">{{item.month}}</div>
						    </swiper-slide>
	            </swiper>
	 	      </div>
	 </div>
</template>
<style scoped lang="less">
	@m:750/375;
	.swiperBox_top{
		 padding-left: 24px*@m;
		 height: 17px*@m;
		 line-height: 17px*@m;
		 font-family: Helvetica-Bold;
		 font-size: 14px*@m;
		 color: #BAC3CE;
		 letter-spacing: 0;
		 margin-top: 15px*@m;
		 margin-bottom: 15px*@m;
	}
	.swiperBox{
		  width: 30px*@m;height: 30px*@m;
		  line-height: 30px*@m;text-align: center;
		  background: gold;
		  border-radius: 50%;
	}
  .aDailyWord{
  	  padding-top: 20px*@m;
  	  width: 100%;position: relative;
  }
  .aDailyWord_wrap{
  	   padding: 0 20px*@m;
  }
  .aDailyWord_calendar{
  	  width: 355px*@m;height:92px*@m;
  	  position:fixed;
  	  bottom: 25px*@m;
  	  left: 10px*@m;
  	  background: #F8F8F8;  
    box-shadow: 0 0 20px*@m 0 rgba(0,0,0,0.04), 0 -10px*@m 31px*@m 0 rgba(0,0,0,0.16);
    border-radius: 7px*@m;
    z-index: 9999;
    overflow: hidden;
  }
</style>

<script>
import { nativeGo, nativeCommentProductLibrarySearch } from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'
import { Subject, } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter } from 'rxjs/operators';
import appMixins from '@/assets/js/appMixins.js'
import DailyBar from '@/components/daily-bar.vue'; //每日一言

export default {
	name:"a-daily-word",
	mixins:[appMixins],
	data () {
    return {
    	    monthList:[
    	        {month:'1'},{month:'2'},{month:'3'},{month:'4'},{month:'5'},{month:'6'},{month:'7'},{month:'8'},{month:'9'},{month:'10'},{month:'11'},{month:'12'}
    	    ],
				dataList:[
				  {year:'2017',month:'4',day:'23',content:'科幻小说构想一个可能性的世界，人站在这个世界的边缘',author:'郝景芳《孤独深处》'},
				  {year:'2018',month:'6',day:'28',content:'苹果人针对每个细节都会扪心自问：我们如何才能使用户体验更加简便？我们如何才能做到最好的用户体验。',author:'郝景芳《孤独深处》'},
				  {year:'2017',month:'4',day:'23',content:'科幻小说构想一个可能性的世界，人站在这个世界的边缘，最容易感觉到出世和异化，而出离世界的感觉是最孤独的孤独，此即「孤独深处」。',author:'郝景芳《孤独深处》'},
				  {year:'2017',month:'4',day:'23',content:'科幻小说构想一个可能性的世界，人站在这个世界的边缘',author:'郝景芳《孤独深处》'},
				  {year:'2017',month:'4',day:'23',content:'科幻小说构想一个可能性的世界，人站在这个世界的边缘',author:'郝景芳《孤独深处》'}
				],
				//swiper
				swiperOption: {
			    slidesPerView: 1,
					slidesPerView: 'auto',
			    spaceBetween: 40,
			    slidesPerGroup: 1,
			    loop: true,
			    loopFillGroupWithBlank: true,
			  }
    }
  },
  components:{
     DailyBar
  },
  methods: {
  	   
    
  },
  computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
  },
  mounted(){
  	  
		
  }
}
</script>




