// import DailyBar from '@/components/daily-bar.vue'; //每日一言
<template>
	<div class="DailyBar">
		<div class="DailyBar_box" v-for="(item,index) in dataList" v-bind:key="index">
			<a href="javascript:;" class="DailyBar_box_a">
				<img src="../assets/comment-bar/dd.jpg"  alt="" class="DailyBar_box_img" />
			</a>
			<div class="DailyBar_box_mask"></div>
			<div class="DailyBar_box_text">
				 <div class="DailyBar_box_text_top clearfix">
				 	  <span class="DailyBar_box_day">{{item.day}}</span>
				 	  <div class="DailyBar_box_time">
				 	  	   <p class="DailyBar_box_time_month">{{item.month}}</p>
				 	  	   <p class="DailyBar_box_time_year">{{item.year}}</p>
				 	  </div>
				 </div>
				 <div class="DailyBar_box_content_wrap">
				 	   <div class="DailyBar_box_content_cell">
				 	   	  <div class="DailyBar_box_content_word">
				 	   	  	    {{item.content}}
				 	   	  </div>
				 	   </div>
				 </div>
				 <p class="DailyBar_author">—— {{item.author}}</p>
				 
			</div>
		</div>
	</div>
</template>
<style scoped lang="less">
  @m:750/375;
  .DailyBar{
  	   width: 100%;
  }
  .DailyBar_box{
  	  width: 335px*@m;height: 204px*@m;
  	  border-radius: 6px*@m;
  	  overflow: hidden;
  	  position: relative;
  	  margin-bottom: 25px*@m;
  }
  .DailyBar_box:last-child{
  	  margin-bottom: 0;
  }
  .DailyBar_box_img{
  	   width: 100%;height: 100%;object-fit: cover;
  }
  .DailyBar_box_mask{
  	   width: 100%;height:100%;
  	   position: absolute;top: 0;left: 0;
  	   background-image: linear-gradient(-180deg, rgba(15,37,64,0.55) 2%, rgba(15,37,64,0.85) 100%);
     border-radius: 6px*@m;
  }
  .DailyBar_box_text{
  	   position: absolute;top: 0;left: 0;
  	   z-index: 1;
  }
  .DailyBar_box_text_top{
  	   height: 35px*@m;
  	   margin-left: 15px*@m;
  	   margin-top: 14px*@m;
  }
  .DailyBar_box_day{
  	   float: left;
  	   font-family: Helvetica;
		 font-size: 35px*@m;
		 color: #FFFFFF;
		 letter-spacing: 0;
		 line-height: 35px*@m;
  }
  .DailyBar_box_time{
  	   float: left;
  	   margin-left: 6px*@m;
  	   margin-top: 3px*@m;
  }
  .DailyBar_box_time_month{
  	    font-family: Helvetica;
			font-size: 14px*@m;
			color: #C6C6C6;
			letter-spacing: 0;
			line-height: 14px*@m;
  }
  .DailyBar_box_time_year{
  	  font-family: Helvetica;
		font-size: 12px*@m;
		color: #C6C6C6;
		letter-spacing: 0;
		line-height: 12px*@m;
		margin-top: 2px*@m;
  }
   .DailyBar_box_content_wrap{
   	  display: table;
   	  width: 275px*@m;
   	  margin-left: 30px*@m;
   	  height: 88px*@m;
   	  margin-top: 16px*@m;
   	  overflow: hidden;
   }
   .DailyBar_box_content_cell{
   	  display: table-cell;
   	  vertical-align: middle;
   }
   .DailyBar_box_content_word{
   	  font-family: PingFangSC-Semibold;
			font-size: 16px*@m;
			color: #FFFFFF;
			letter-spacing: 0;
			line-height: 22px*@m;
			overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
			
   }
   .DailyBar_author{
   	  font-family: PingFangSC-Regular;
			font-size: 13px*@m;
			color: #C6C6C6;
			letter-spacing: 0;
			text-align: right;
			margin-top: 16px*@m;
   }
 

</style>

<script>


export default {
  name: 'DailyBar',
	components: {  },
  props: ['dataList'],
  data () {
    return {
    	   
    }
  },
  methods:{
		
  },
  mounted(){
  }

}
</script>


